import React from 'react'

import useMediaQuery from '@mui/material/useMediaQuery'
import { styled } from '@mui/material/styles'
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import { theme } from '../constants/Themes'
import Zoom from 'react-reveal/Zoom'

export const StyledCarouselCaption = styled("div")(
  ({ fontSize }) => ({
      position: 'absolute',
      backgroundColor: 'rgba(0, 0, 0, 0.7)',
      color: 'white',
      fontSize,
  })
)

export const StyledCarouselAutoPlayCaption = styled(StyledCarouselCaption)`
  top: 0px;
  right: 0px;
  padding: 0px;
  z-index: 4;
  display: flex;
  cursor: pointer;
  font-size: 72px;
  border-radius: 0px 0px 0px 10px;
  line-height: 0px;
`

export const CarouselAutoPlayControl = ({ isAutoPlayEnabled, setAutoPlayEnabled }) => {
  const isBtMd = useMediaQuery(theme.breakpoints.up('md'))

  return (
    <StyledCarouselAutoPlayCaption onClick={() => setAutoPlayEnabled(prev => !prev)}>
    <Zoom duration={2000}>
      {isAutoPlayEnabled ?
          <PauseIcon fontSize={isBtMd ? "inherit" : "large"} />
        : <PlayArrowIcon fontSize={isBtMd ? "inherit" : "large"} />
      }
      </Zoom>
    </StyledCarouselAutoPlayCaption>
  )
}

export const StyledCarouselSectionContainer = styled('div')`
  display: flex;
  flex-direction: column;
  position: relative;
`
