import React, { useState, useRef, useEffect } from 'react'

import { map, get, some, set } from 'lodash'
import useMediaQuery from '@mui/material/useMediaQuery'
import { styled } from '@mui/material/styles'
import Divider from '@mui/material/Divider'
import { Carousel } from 'react-responsive-carousel'
import {Img} from 'react-image'
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';

import { RevealingDimSection, DarkHeader } from '../components/Section'
import { CarouselAutoPlayControl, StyledCarouselSectionContainer } from '../components/Carousel'
import { theme } from '../constants/Themes'
import MOVIES, { getMoviePosterUrls, getMovieTitle } from '../constants/Movies'
import TV_SHOWS, { getTvPosterUrls, getTvShowTitle } from '../constants/TvShows'
import { useOnScreen } from '../utils/onScreenHook'
import { CAROUSEL_LOAD_DISTANCE } from '../config/settings'

const StyledCarouselPage = styled("div")(
    ({ fontSize }) => ({
        backgroundColor: '#000000',
        height: '100%',
        color: 'white',
        fontSize,
    })
)

const StyledCarousel = styled(Carousel)`
    .carousel-status {
        color: white;
        right: auto;
        font-size: 24px;
        margin-top: 0px;
        padding-left: 4px;
        padding-right: 8px;
        border-radius: 0px 0px 10px 0px;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 4;
    }
`

const StyledCarouselSection = styled('div')`
    display: flex;
    flex-direction: column;
    background-color: black;
    margin: 4px;
    margin-top: max(10vh, calc((100vh - 64px - min(calc(88vw*6/4), 88vh))/3));
    margin-bottom: max(10vh, calc((100vh - 64px - min(calc(88vw*6/4), 88vh))/3));
`

const StyledCarouselTitle= styled('div')`
    display: flex;
    background-color: black;
    color: white;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center;
    border-bottom: 2px #f2f1f0 dashed;
`

const StyledCarouselSubContainer = styled('div')`
    position: relative;
`

const StyledDarkHeader = styled(DarkHeader)`
    margin-bottom: 0px;
    padding-bottom: 0px;
`

const isNoPosterLoading = (loadingTracker) => !some(loadingTracker)

const StatusReportingCircularProgress = ({ index, setLoading, ...rest }) => {
    useEffect(() => {
        setLoading(prev => set([...prev], index, true))
        return () => setLoading(prev => set([...prev], index, false))
    }, [setLoading, index])
    return <CircularProgress color="inherit" {...rest}/>
}

const EntertainmentSection = ({ innerRef }) => {
    const [isMoviesAutoPlayEnabled, setMoviesAutoPlayEnabled] = useState(true)
    const [isTvShowsAutoPlayEnabled, setTvShowsAutoPlayEnabled] = useState(true)
    const [currentTvIndex, setCurrentTvIndex] = useState(0)
    const [currentMovieIndex, setCurrentMovieIndex] = useState(0)
    const [tvPosterLoadingTracker, setTvPosterLoadingTracker] = useState([])
    const [moviePosterLoadingTracker, setMoviePosterLoadingTracker] = useState([])

    const isBtMd = useMediaQuery(theme.breakpoints.up('md'))
    const pictureWidth = "min(88vw, 88vh*4/6)"
    const pictureHieght = "min(calc(88vw*6/4), 88vh)"
    const fontSize = isBtMd ? 22 : 16

    const tvCarouselSectionRef = useRef()
    const tvCarouselIsVisible = useOnScreen(tvCarouselSectionRef)

    const movieCarouselSectionRef = useRef()
    const movieCarouselIsVisible = useOnScreen(movieCarouselSectionRef)

    return (
        <div ref={innerRef}>
            <RevealingDimSection>
                <StyledDarkHeader> Entertainment </StyledDarkHeader>
                <StyledCarouselSectionContainer>
                    <StyledCarouselSection ref={tvCarouselSectionRef}>
                        <StyledCarouselTitle>TV Show Rankings</StyledCarouselTitle>
                        <StyledCarouselSubContainer>
                            <CarouselAutoPlayControl isAutoPlayEnabled={isTvShowsAutoPlayEnabled} setAutoPlayEnabled={setTvShowsAutoPlayEnabled} />
                            <StyledCarousel
                                onChange={(index) => setCurrentTvIndex(index)}
                                autoPlay={
                                    isNoPosterLoading(tvPosterLoadingTracker)
                                    && isTvShowsAutoPlayEnabled
                                    && tvCarouselIsVisible
                                }
                                infiniteLoop={true}
                                interval={4000}
                                showIndicators={false}
                                useKeyboardArrows={true}
                                width={pictureWidth}
                                showThumbs={false}
                                showStatus={true}
                                statusFormatter={(current) => `#${current}`}
                                stopOnHover={false}
                                preventMovementUntilSwipeScrollTolerance={true}
                                swipeScrollTolerance={30}
                            >
                                {map(TV_SHOWS, (tvShowData, index) => {
                                const posterKey = get(tvShowData, 'poster')
                                const tvShowTitle = getTvShowTitle(index)
                                return (
                                    <StyledCarouselPage fontSize={fontSize} key={`TV_SHOW_${posterKey}_${index}`}>
                                        <div style={{ height: pictureHieght, display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }}>
                                            {(index - currentTvIndex < CAROUSEL_LOAD_DISTANCE) &&
                                                <Img
                                                    loader={
                                                        <StatusReportingCircularProgress
                                                            index={index}
                                                            setLoading={setTvPosterLoadingTracker}
                                                            color="inherit"
                                                        />
                                                    }
                                                    unloader={
                                                        <Alert severity="error">
                                                            Could not load the poster of {tvShowTitle}
                                                        </Alert>
                                                    }
                                                    alt={tvShowTitle}
                                                    width={pictureWidth}
                                                    height={pictureHieght}
                                                    src={getTvPosterUrls(posterKey)}
                                                    style={{ width: pictureWidth, height: pictureHieght }}
                                                />
                                            }
                                        </div>
                                    </StyledCarouselPage>
                                )
                                })}
                            </StyledCarousel>
                        </StyledCarouselSubContainer>
                    </StyledCarouselSection>
                    <Divider />
                    <StyledCarouselSection ref={movieCarouselSectionRef}>
                        <StyledCarouselTitle>Movie Rankings</StyledCarouselTitle>
                        <StyledCarouselSubContainer>
                            <CarouselAutoPlayControl isAutoPlayEnabled={isMoviesAutoPlayEnabled} setAutoPlayEnabled={setMoviesAutoPlayEnabled} />
                            <StyledCarousel
                                onChange={(index) => setCurrentMovieIndex(index)}
                                autoPlay={
                                    isNoPosterLoading(moviePosterLoadingTracker)
                                    && isMoviesAutoPlayEnabled
                                    && movieCarouselIsVisible
                                }
                                infiniteLoop={true}
                                interval={4000}
                                showIndicators={false}
                                useKeyboardArrows={true}
                                width={pictureWidth}
                                showThumbs={false}
                                showStatus={true}
                                statusFormatter={(current) => `#${current}`}
                                stopOnHover={false}
                                preventMovementUntilSwipeScrollTolerance={true}
                                swipeScrollTolerance={30}
                            >
                                {map(MOVIES, (movieData, index) => {
                                const posterKey = get(movieData, 'poster')
                                const movieTitle = getMovieTitle(index)
                                return (
                                    <StyledCarouselPage fontSize={fontSize} key={`MOVIE_${posterKey}_${index}`}>
                                        <div style={{ height: pictureHieght, display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }}>
                                            {(index - currentMovieIndex < CAROUSEL_LOAD_DISTANCE) &&
                                                <Img
                                                    loader={
                                                        <StatusReportingCircularProgress
                                                            index={index}
                                                            setLoading={setMoviePosterLoadingTracker}
                                                            color="inherit"
                                                        />
                                                    }
                                                    unloader={
                                                        <Alert severity="error">
                                                            Could not load the poster of {movieTitle}
                                                        </Alert>
                                                    }
                                                    alt={movieTitle}
                                                    width={pictureWidth}
                                                    height={pictureHieght}
                                                    src={getMoviePosterUrls(posterKey)}
                                                    style={{ width: pictureWidth, height: pictureHieght }}
                                                />
                                            }
                                        </div>
                                    </StyledCarouselPage>
                                )
                                })}
                            </StyledCarousel>
                        </StyledCarouselSubContainer>
                    </StyledCarouselSection>
                </StyledCarouselSectionContainer>
            </RevealingDimSection>
        </div>
    )
}

export default EntertainmentSection